@font-face {
  font-family: 'Gordita';
  src: url('./assets/fonts/hinted-GorditaRegular.ttf') format('truetype')
      url('./assets/fonts/hinted-GorditaBold.ttf') format('truetype'),
    url('./assets/fonts/hinted-GorditaMedium.ttf') format('truetype');
}

@font-face {
  font-family: 'Fakt';
  src: url('./assets/fonts/fakt.ttf') format('truetype');
}

:root {
  --font-family: 'Gordita';
  --label-font-size: 0.87rem;
  --label-top: -0.87rem;
  --input-font-size: 1.5rem;
  --input-font-weight: 300;
  --text-color-dark: rgba(0, 0, 0, 0.9);
  --text-color: rgba(0, 0, 0, 0.7);
  --text-color-light: rgba(0, 0, 0, 0.5);
  --hover-color: rgba(0, 0, 0, 0.03);
  --side-bar-left-padding: 1.8rem;
  --border-color: rgba(27, 26, 25, 0.05);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-family);

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
